@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&family=Poppins:wght@100;200;300;400;500;600;700;800;900&family=Roboto+Flex:opsz,wght@8..144,100;8..144,200;8..144,300;8..144,400;8..144,500;8..144,600;8..144,700;8..144,800;8..144,900;8..144,1000&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    /* font-family: 'Roboto Flex', sans-serif; */
    font-family: 'Spoqa Han Sans Neo', 'sans-serif';
    font-size: 1em;
    font-weight: 400;
}

/* https://coolors.co/palette/e63946-f1faee-a8dadc-457b9d-1d3557 */
.header {
    position: sticky;
    top: 0;
    background: white;
    z-index: 4;
    box-shadow: 0px 0 5px rgba(0, 0, 0, 0.3);
}

.fa-bars {
    position: fixed;
    top: 0vh;
    left: 0;
    /* border: 1px dashed red; */
    font-size: 1.5em;
    padding: 1.5vh;

}

.header h1 {
    text-align: center;
    font-size: 1.5em;
    letter-spacing: 5px;
    padding: 1vh;
    font-weight: 700;
    color: #1d3557;
    /* border: 1px dashed red; */
}

.container-icon {
    display: grid;
    gap: 0vw;
    grid-template-columns: repeat(4, minmax(25%, 1fr));
    margin: 0vh 0;
    padding: 4vh 8vw 2vh 8vw;
    background: #bde0fe;
}

.sidemenu-bg {
    background: rgba(0, 0, 0, .5);
    position: fixed;
    left: 70vw;
    top: 0;
    width: 30vw;
    height: 100vh;
    z-index: 9;
    /* border: 1px dashed red; */
}
.container-sidemenu {
    position: fixed;
    left: 0;
    top: 0;
    width: 70vw;
    height: 100vh;
    z-index: 10;
    background: white;
}
.container-sidemenu .x {
    color: #1d3557;
    position: relative;
    top:0;
    line-height: 10vh;
    font-size: 7vh;
    text-align: right;
    padding-right: 3vw;
}
.container-sidemenu .sidemenu-title {
    /* height: 20vh; */
    /* border: 1px dashed red; */
    justify-content: center;
    align-items: center;
    padding: 2vh 10vw;
    text-align: left;
}
.container-sidemenu .sidemenu-title p {
    text-align: center;
    font-weight: 500;
}
.container-sidemenu .sidemenu-icons {
    position: relative;
    display: grid;
    gap: 0vw;
    grid-template-columns: repeat(2, minmax(50%, 1fr));
    border-top: 0.5px solid #1d3557;
    /* border-bottom: 0.5px solid #1d3557; */
    padding: 10vw;
}
.container-sidemenu .sidemenu-footer {
    position: absolute;
    bottom: 0;
    height: 10vh;
    width: 100%;
    background: #1d3557;
    color: white;
    /* border: 1px dashed red; */
    font-size: 5vh;
    padding: 2.5vh;
    text-align: right;
}

.icon-frame {
    /* border: 1px dashed red; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 0.8em;
    margin: 1vh 0;
}

:is(.container-icon, .container-sidemenu) .icon-frame:nth-child(4n-3) .icon{
    background-color: #ffadad;
}

:is(.container-icon, .container-sidemenu) .icon-frame:nth-child(4n-2) .icon{
    background-color: #ffd6a5;
}

:is(.container-icon, .container-sidemenu) .icon-frame:nth-child(4n-1) .icon{
    background-color: #caffbf;
}

:is(.container-icon, .container-sidemenu) .icon-frame:nth-child(4n) .icon{
    background-color: #a8dadc;
}

.icon {
    background: white;
    width: 54px;
    height: 54px;
    border-radius: 17px;
    margin: 0px;
    overflow: hidden;
    cursor: pointer;
}

.icon i {
    /* color: #1d3557; */
    color: white;
    font-size: 30px;
    line-height: 54px;
    /* border: 1px dashed blue; */
}


.icon-frame p {
    margin-top: 4px;
    /* border: 1px dashed blue; */

}

.waist {
    position: relative;
    margin: 2vh 0;
    overflow-x: hidden;
    /* border: 2px dashed red; */
}

.container-news {
    /* background: #bde0fe; */
    padding: 5vh 0 0vh 0;
}

.news {
    display: flex;
    flex-direction: column;
    margin: 0 5vw;
    list-style: none;
}

.news li {
    border-bottom: 0.5px solid #1d3557;
    margin: 1vh 0 0 0;
    padding: 1vh 0;
}

.logo {
    color: #1d3557;
    font-size: 7vw;
    font-weight: 600;
    margin: 5vw 0 40px 0;
    letter-spacing: 0vw;
    display: flex;
    justify-content: center;
    align-items: center;
}

.title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* border: 1px dashed red; */
    margin: 0vh 0;
}

.title h1 {
    font-size: 1.5em;
    font-weight: 700;
    letter-spacing: 0px;
    text-align: left;
    margin: 0vh 6vw;
    color: #1d3557;
    /* background: linear-gradient( #1d3557 50%, #a8dadc 100%); */
    /* background-clip: text; */
    /* color: transparent; */
}

.title span {
    margin: 3vw 6vw 3vw 0;
    padding-top: 3vw;
    /* border: 1px dashed red; */
    color: #1d3557;
    cursor: pointer;
}

.title span i {
    margin-right: 1vw;
}

.gridBx-frame {
    display: grid;
    justify-content: center;
    /* border: 1px dashed blue; */
}

.gridBx {
    display: grid;
    gap: 2vw;
    grid-template-columns: repeat(3, minmax(240px, 1fr));
    /* border: 1px dashed green; */
    width: 90vw;
    height: auto;
    /* margin: 5vh 5vw; */
}

.item {
    border: 0.5px solid rgba(0, 0, 0, 0.5);
    width: 100%;
    aspect-ratio: 1/1;
    overflow: hidden;
    border-radius: 10px 10px 0 0;
    color: #f1faee;

}

.item .head {
    height: 15%;
    background: #457b9d;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.head .text-title {
    /* border: 1px dashed red; */
    font-size: 1.2em;
    font-weight: 400;
    padding-left: 3vw;

}

.item .head .ear {
    position: relative;
    right: -5%;
    height: 100%;
    width: 45%;
    border-left: 1vw solid #a8dadc;
    background: white;
    transform: skew(-12deg);
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.5s;
}

.ear .text-label {
    font-size: 1.2em;
    font-weight: 600;
    transform: skew(12deg);
    background: red;
    padding: 4px;
    border-radius: 4px;
    margin-right: 10px;
    /* border: 1px dashed red; */
}

.item .body {
    height: 63%;
    background: null;
    transition: 0.5s;
    overflow-y: hidden;
}

.item:hover .body {
    background: #f1faee;
}

.item:hover .head .ear {
    background: #f1faee;
}

.item .body img {
    position: relative;
    top: 0vw;
    height: 100%;
    object-fit: cover;
    object-position: center;
}

.item .leg {
    height: 22%;
    background: #457b9d;
    display: flex;
    align-items: center;
    justify-content: right;
    padding: 0 2%;
}

.leg .text-main {
    position: relative;
    left: 0%;
    list-style: none;
    width: 40%;
}

li {
    margin: 5px 0;
    font-size: 1em;
    text-align: left;
}

.item i {
    width: 20%;
    text-align: center;
    margin: 0 10px 0 0;
}

@media (max-width: 450px) {
    .gridBx {
        grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
    }
}

@media (max-width: 600px) {
    .gridBx {
        grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
    }

    /* .head .text-title {
        border: 1px dashed red;
        font-size: 1em;
    }
    .ear .text-label {
        font-size: 1em;
    }
    li {
        font-size: 1em;
    } */
}

@media (max-width: 1024px) {
    .gridBx {
        grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
        /* margin: 5vh 5vw; */
    }

