    }
    li {
        font-size: 0.8em;
    } */

}

/* https://coolors.co/palette/fbf8cc-fde4cf-ffcfd2-f1c0e8-cfbaf0-a3c4f3-90dbf4-8eecf5-98f5e1-b9fbc0 */




.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}


.frame {
    padding: 15px;
    /* height: 300px; */
    width: 100%;
    box-shadow:
        6px 6px 15px rgba(0, 0, 0, .5),
        -1px -1px 2px rgba(255, 255, 255, 1);
}

.inner-frame {
    /* height: 270px; */
    width: 100%;
    border-top: 25px solid rgba(0, 0, 0, .19);
    border-left: 25px solid rgba(0, 0, 0, .15);
    border-right: 25px solid rgba(0, 0, 0, .08);
    border-bottom: 25px solid rgba(0, 0, 0, .05);
    box-shadow: -1px -1px 1px rgba(0, 0, 0, .6),
        1px 1px 2px rgba(255, 255, 255, 1);
}

.mat {
    width: 100%;
    /* height: 40vh; */
    border-top: 2px solid rgba(0, 0, 0, .3);
    border-left: 2px solid rgba(0, 0, 0, .3);
    border-bottom: 2px solid rgba(0, 0, 0, .1);
    border-right: 2px solid rgba(0, 0, 0, .05);
    margin: 0px auto;
    padding: 0;
    background-color: #fff
}

.img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
}


.photo-inform {
    position: relative;
    width: fit-content;
    right: -32vw;
    /* border: 0.5px solid black; */
    border-radius: 2px;
    margin: 2vh auto 4vh auto;
    padding: 8px 8px 8px 16px;
    text-align: right;
    font-size: 0.5em;
    box-shadow: 5px 5px 20px rgba(0, 0, 0, .25);
    background: black;
    color: white;
    height: fit-content;
}

/* ALBUM SWIPER */

.container-album {
    /* background: #fefae0; */
    padding: 5vh 0 2vh 0;
}
.albumSwiper {
    margin: 3vh 0;

}
/* TOY SWIPER */

.container-toy {
    background: #bdb2ff;
    padding: 5vh 0 5vh 0;
}

.cardSwiper {
    width: 75vw;
    height: 75vw;
    margin: 3vh 0;
}

.cardSwiper .swiper-slide {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 18px;
    font-size: 22px;
    font-weight: bold;
    color: #fff;
}

.product-card {
    /* border: 1px dashed green; */
}

.product-card img {
    /* border: 1px dashed red; */
    width: 100%;
    object-fit: cover;
}

.product-card p {
    position: absolute;
    bottom: 0;
    border-radius: 0 0 18px 18px;
    /* border: 0.5px solid #1d3557; */
    padding: 3vw;
    width: 100%;
    text-align: center;
    color: #1d3557;
    background: white;
    font-size: 0.7em;
    /* font-weight: 200; */
}

.product-card p span {
    font-weight: 600;
    /* font-size: 1.2em; */
}

.product-card .kind-label {
    position: absolute;
    top: 0;
    right: 0;
    height: 5vh;
    font-weight: 600;
    background: red;
    border-radius: 4px;
    margin: 5vw;
    padding: 0 5px;
    /* border: 1px dashed black; */
    line-height: 5vh;
}

/* https://coolors.co/palette/ffadad-ffd6a5-fdffb6-caffbf-9bf6ff-a0c4ff-bdb2ff-ffc6ff-fffffc */

.cardSwiper .swiper-slide:nth-child(1n) {
    background-color: #ffadad;
}

.cardSwiper .swiper-slide:nth-child(2n) {
    background-color: #ffd6a5;
}

.cardSwiper .swiper-slide:nth-child(3n) {
    background-color: #fdffb6;
}

.cardSwiper .swiper-slide:nth-child(4n) {
    background-color: #caffbf;
}

.cardSwiper .swiper-slide:nth-child(5n) {
    background-color: #9bf6ff;
}

.cardSwiper .swiper-slide:nth-child(6n) {
    background-color: #a0c4ff;
}

.swiper-slide:nth-child(7n) {
    background-color: #bdb2ff;
}

.page-title {
    padding: 2vh;
}

.page-title h1 {
    font-size: 1.5em;
    font-weight: 700;
    letter-spacing: 5px;
    color: #1d3557;
}

.news-line {
    /* border: 1px dashed #1d3557; */
    border-left: 4px solid #1d3557;
    height: 15vh;
    margin: 0 5vh;
}

.news-line .news-point {
    position: relative;
    top: 50%;
    left: -0.5vw;
    width: 3vw;
    height: 3vw;
    border-radius: 50%;
    /* border: 1px solid #1d3557; */
    background: #1d3557;
    transform: translate(-50%, -50%);
}

.news-line .news-content {
    position: relative;
    top: 50%;
    /* border: 1px dashed green; */
    display: flex;
    transform: translate(0%, -100%);

}

.news-line .news-date {
    position: relative;
    top: 25%;
    left: 0vw;
    text-align: left;
    padding-left: 5vw;
    font-weight: 600;
}

.news-line .news-title {
    position: relative;
    top: 25%;
    left: 0vw;
    text-align: left;
    padding-left: 5vw;
}

.footer-inform {
    padding: 2vh;
    width: 100%;
    font-size: 0.5em;
    text-align: left;
    background: #1d3557;
color: white;
}
.footer-inform h1 {
    font-weight: 800;
}
.footer-inform p {
    margin: 3px 0 0 0;
}
.nav-footer {
    position: sticky;
    bottom: 0;
    background: white;
    z-index: 1;
    /* border: 1px dashed red; */
    width: 100%;
    box-shadow: 0px 0 5px rgba(0, 0, 0, 0.3);
}


.menu ul {
    display: flex;
    justify-content: space-evenly;
    /* border: 1px dashed red; */
    margin: 0vh 0vw;
    border-top: 1px solid white;
}

.menu ul li {
    list-style: none;
    width: 25vw;
    text-align: center;
    margin: 0vw;
    font-size: 2em;
    transition: 0.5s;
    /* border: 1px dashed red; */

}

.menu li i {
    color: #1d3557;
    /* border: 1px dashed red; */
    margin: 5px 0 0 0;
}

.menu li p {
    font-size: 0.4em;
    font-weight: 400;
    margin: 0 0 3px 0;
    color: #1d3557;
}

a {
    text-decoration: none;
}

.menu ul li:hover {
    background: #457b9d;
}

.menu li:hover i {
    color: #f1faee;
}

.menu li:hover p {
    color: #f1faee;
}
